#app {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#app .top {
  position: fixed;
  left: 0;
  top: 0;
  height: 0.333333rem;
  width: 100%;
  background-color: #050505;
}


